@import 'variables';

html,
body {
  height: 100vh;
}

[v-cloak] {
  display: none;
}

body {
  font-family: 'Source Sans Pro', 'Helvetica Neue', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  line-height: 1.42857143;
  color: #333;
  background-color: #fff;

  background: $blue;
  background: $gradient;
  display: flex;
  align-items: center;
  justify-content: center;
}

#app {
  text-align: center;
  max-width: 50em;
}

h1 {
  font-size: 63px;
  margin-top: 20px;
  margin-bottom: 10px;
  display: inline-block;
  font-family: 'Dosis', 'Source Sans Pro', 'Helvetica Neue', Arial, sans-serif;
  font-weight: 300;
  line-height: 1;
  padding-right: 80px;
  background-position: center right;
  background-repeat: no-repeat;
  background-size: 55px auto;
  background-image: url("data:image/png;base64,");
}

p.lead {
  font-size: 1.8em;
  margin: 1em 0;
  font-weight: 200;
  line-height: 1.4;
}

.accolades a {
  margin-left: 0;
  margin-right: 5px;
}

.btn {
  display: inline-block;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-image: none;
  text-decoration: none;
}

.feature-list {
  display: flex;
  justify-content: center;

  ul {
    padding-left: 0;
    margin: 2em 2em;
    text-align: left;
  }

  + p {
    margin-top: 0;
    margin-bottom: 2em;
  }
}

code {
  color: $code-grey;
  font-family: 'Fira Code', 'monospace';
  display: inline-block;
  padding: .5em;
  margin-top: 1.5em;
  margin-bottom: 1em;
  border-radius: 5px;
  background: rgba(255,255,255,.5);
  font-size: 20px;
}

.cta .btn {
  margin: 0 .5em;
}

.btn-outline {
  padding: 10px 16px;
  font-size: 18px;
  margin-bottom: 0;
  background: none;
  line-height: 1.3333333;
  border-radius: 6px;
  border: 2px solid #91ddec;
  color: #147688;

  &.btn-lg {
    border-width: 3px;
  }

  &:hover {
    color: #fff;
    border-color: rgb(65, 184, 131);
    background-color: rgba(65, 184, 131, 0.67);
  }

  &:active,
  &:focus {
    border-color: rgb(65, 184, 131);
    background-color: rgba(65, 184, 131, 1);
  }
}

.content {
  transition: opacity .25s;
}

.hidden {
  opacity: 0;
}

.dropdown-action {
  margin-top: 5em;
}


